<template>
  <div class="main">
    <div class="page-title">Share your GitHub project on your social platform</div>
    <div class="page-tips">Please enter your GitHub project address to query project information</div>
    <div class="page-input">
      <input class="input" type="text" v-model="address">
    </div>
    <div class="page-submit" @click="submit">Generate</div>
    <div class="page-project" id="project-Canvas" v-if="project.full_name">
      <div class="head">
        <div class="name">{{project.full_name.split('/')[0] + '/'}}<span>{{project.full_name.split('/')[1]}}</span></div>
        <img class="img" :src="project.owner.avatar_url" alt="">
      </div>
      <div class="introduction">{{project.description}}</div>
      <div class="data">
        <div class="item">
          <img src="../../assets/Contributors.png" alt="">
          <div class="text">
            <p>{{project.subscribers_count}}</p>
            <span>Contributors</span>
          </div>
        </div>
        <div class="item">
          <img src="../../assets/Issue.png" alt="">
          <div class="text">
            <p>{{project.open_issues}}</p>
            <span>Issue</span>
          </div>
        </div>
        <div class="item">
          <img src="../../assets/Stars.png" alt="">
          <div class="text">
            <p>{{project.watchers}}</p>
            <span>Stars</span>
          </div>
        </div>
        <div class="item">
          <img src="../../assets/Forks.png" alt="">
          <div class="text">
            <p>{{project.forks}}</p>
            <span>Forks</span>
          </div>
        </div>
        <div class="git-logo">
          <img src="../../assets/github-fill.png" alt="">
        </div>
      </div>
      <div class="line"></div>
    </div>
    <div class="button" v-if="project.full_name">
      <div class="item" @click="downloadImg">Download</div>
      <div class="item" @click="downloadImg">Share to</div>
    </div>
    
  </div>
</template>
<script>
import service from "../../api/api.js"
import html2canvas from "html2canvas"
export default {
  name: 'HelloWorld',
  data () {
    return {
      address: '',
      project: {}
    }
  },
  methods: {
    submit() {
      let path = this.address.substring(this.address.indexOf('github.com') + 10)
      service.get('https://api.github.com/repos' + path).then(res => {
        this.project = res
      }).catch(error => {
        console.log('error', error);
        alert('Please enter the correct address！')
      });
    },
    async downloadImg() {
      let element = document.getElementById('project-Canvas'); // 要转换成图片的页面元素
      let canvas = await html2canvas(element, {
        useCORS: true,
      });
      let img = canvas.toDataURL('image/png');

      // 创建一个a标签模拟点击进行下载
      let a = document.createElement('a');
      a.href = img;
      a.download = 'sharePic.png';
      a.click();
    },
  },
  mounted() {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style type="text/less" scoped>
.main{
  min-width: 100vw;
  min-height: 100vh;
  background-color: #000;
  display: flex;
  flex-direction: column;
  align-items: center;
  .page-title{
    width: 600px;
    margin-top: 32px;
    color: #fff;
    font-size: 44px;
    font-weight: 600;
    text-align: center;
  }
  .page-tips{
    width: 680px;
    margin-top: 24px;
    color: #fff;
    font-size: 20px;
    text-align: center;
  }
  .page-input{
    width: 440px;
    height: 48px;
    margin-top: 40px;
    border: solid 1px #fff;
    border-radius: 4px;
    .input{
      box-sizing: border-box;
      width: 440px;
      height: 48px;
      padding: 0 8px;
      color: #fff;
      font-size: 18px;
      background: none;
      outline: none;
      border: none;
    }
  }
  .page-submit{
    width: 130px;
    height: 36px;
    margin-top: 24px;
    color: #fff;
    font-size: 18px;
    text-align: center;
    line-height: 36px;
    background-color: #2253d5;
    border-radius: 4px;
    cursor: pointer;
  }
  .page-project{
    box-sizing: border-box;
    width: 500px;
    height: 258px;
    margin-top: 40px;
    padding: 20px 0;
    background-color: #fff;
    border: solid 1px #fff;
    .head{
      box-sizing: border-box;
      width: 100%;
      height: 80px;
      padding: 0 32px;
      display: flex;
      justify-content: space-between;
      align-items: center;
      .name{
        width: 280px;
        color: #444;
        font-size: 32px;
        letter-spacing: 0.2px;
        line-break: anywhere;
        >span{
          font-weight: 600;
        }
      }
      >img{
        width: 68px;
        height: 68px;
      }
    }
    .introduction{
      width: 430px;
      height: 40px;
      margin: 16px 32px;
      color: #999;
      font-size: 16px;
    }
    .data{
      box-sizing: border-box;
      position: relative;
      width: 430px;
      height: 40px;
      margin: 24px 32px 20px;
      display: flex;
      .item{
        margin-right: 24px;
        display: flex;
        justify-content: space-between;
        >img{
          width: 16px;
          height: 16px;
          margin-right: 4px;
          margin-top: 4px;
        }
        .text{
          >p{
            height: 20px;
            padding: 0;
            margin: 0;
            color: #444;
            font-size: 16px;
            line-height: 24px;
          }
          >span{
            color: #888;
            font-size: 12px;
          }
        }
      }
      .git-logo{
        position: absolute;
        top: 8px;
        right: 0;
        >img{
          width: 24px;
          height: 24px;
        }
      }
    }
    .line{
      width: 100%;
      height: 12px;
      margin-top: 12px;
      background-color: rgb(138,223,83);
    }
  }
  .button{
    width: 480px;
    margin-top: 16px;
    display: flex;
    justify-content: space-between;
    .item{
      width: 130px;
      height: 32px;
      color: #fff;
      font-size: 14px;
      line-height: 30px;
      text-align: center;
      border: solid 1px #fff;
      border-radius: 6px;
      cursor: pointer;
    }
  }
}
</style>



